export function initTypes(callback){
    if(platform=='browser'){
        $.ajax({
            url:'./data/data_new.json',
            dataType:'json',
            error:function(err){
                // console.log(err);
                // console.log('shibai');
                initTypes(callback);
            },
            success:function(datas){
                var types = [];
                for(var i in datas){
                    var data = datas[i];
                    var exist = false;
                    var type;
                    if(types.length==0){
                        type = {};
                        type.name = data.type;
                        type.tag = data.tags;
                        types.push(type);
                    }else{
                        type = {};
                        var num;
                        for(var j in types){
                            var type0 = types[j];
                            type=type0;
                            if(type0.name==data.type){
                                exist = true;
                                num = j;
                                for(var m in data.tags){
                                    var tag = data.tags[m];
                                    if(!(type0.tag.toString().indexOf(tag)>-1)){
                                        var tags = [];
                                        tags = type0.tag;
                                        tags.push(tag);
                                        type = {};
                                        type.name = type0.name;
                                        type.tag = tags;
                                    }
                                }
                            }
                        }
                        if(!exist){
                            type={};
                            type.name = data.type;
                            type.tag = data.tags;
                            jsons = types;
                            jsons.push(type);
                        }else{
                            var jsons = [];
                            for(var j in types){
                                if(j==num){
                                    jsons.push(type);
                                }else{
                                    jsons.push(types[j]);
                                }
                            }
                        }
                        types = jsons;
                    }
                }
                for(i in types){
                    types[i].id = parseInt(i);
                }
                // console.log(types);
                // global.datas.configs = types;
                callback(types);
            }
        })
    }else{
        $.ajax({
            url:'http://localhost:8080/ipns/Qmbg2F7oQHrrbbkvZekKZ9USTMap513KBo8Q17VmpAXhq4/data/data_new.json',
            dataType:'json',
            error:function(err){
                // console.log(err);
                // console.log('shibai');
                initTypes(callback);
            },
            success:function(datas){
                var types = [];
                for(var i in datas){
                    var data = datas[i];
                    var exist = false;
                    var type;
                    if(types.length==0){
                        type = {};
                        type.name = data.type;
                        type.tag = data.tags;
                        types.push(type);
                    }else{
                        type = {};
                        var num;
                        for(var j in types){
                            var type0 = types[j];
                            type=type0;
                            if(type0.name==data.type){
                                exist = true;
                                num = j;
                                for(var m in data.tags){
                                    var tag = data.tags[m];
                                    if(!(type0.tag.toString().indexOf(tag)>-1)){
                                        var tags = [];
                                        tags = type0.tag;
                                        tags.push(tag);
                                        type = {};
                                        type.name = type0.name;
                                        type.tag = tags;
                                    }
                                }
                            }
                        }
                        if(!exist){
                            type={};
                            type.name = data.type;
                            type.tag = data.tags;
                            jsons = types;
                            jsons.push(type);
                        }else{
                            var jsons = [];
                            for(var j in types){
                                if(j==num){
                                    jsons.push(type);
                                }else{
                                    jsons.push(types[j]);
                                }
                            }
                        }
                        types = jsons;
                    }
                }
                for(i in types){
                    types[i].id = parseInt(i);
                }
                // console.log(types);
                // global.datas.configs = types;
                callback(types);
            }
        })
    }

}

