var timejob = 0;
export function showADSCPV(){
    var time = new Date().getTime();
    var ads = 'http://www.hpips.net/home/ads';
    var adscpm = 'http://www.hpips.net/home/adscpm';
    var adscpc = 'http://www.hpips.net/home/adscpc';
    var adsydl = 'http://www.hpips.net//home/ydlads';
    var limit = 30*60*1000;
    // limit = 20*1000;
    $("#iframe_ads_1").attr('src','');
    $("#iframe_ads_1").attr('src',adscpc+'?t='+time);
    $("#div_ads_1").removeClass('DISPLAYNONE');
    $("#iframe_ads_2").attr('src','');
    $("#iframe_ads_2").attr('src',adscpc+'?t='+time);
    $("#div_ads_2").removeClass('DISPLAYNONE');
    $("#iframe_ads_cpm").attr('src','');
    $("#iframe_ads_cpm").attr('src',adscpm+'?t='+time);
    $("#iframe_ads_cpc").attr('src','');
    $("#iframe_ads_cpc").attr('src',adscpc+'?t='+time);
    $("#iframe_ads_ydl_mobi").attr('src','');
    $("#iframe_ads_ydl_mobi").attr('src',adsydl+'?t='+time);
    LocalForage.getItem('adscpv',function (err,value) {
        var status = false;
        if(err) {
            console.log(err);
        }else {
            if(value==null){
                status = true;
            }else{
                if((time-value)>limit){
                    status = true;
                }
            }
            console.log(status);
            if(status){
                $("#iframe_ads_cpv").attr('src','');
                $("#iframe_ads_cpv").attr('src',ads+'?t='+time);
                $("#div_ads_cpv").show();
                LocalForage.setItem('adscpv',time);
                var times = 3;
                clearTimeout(timejob);
                timejob = setInterval(function () {
                    times = times - 1;
                    if(times==0){
                        $("#iframe_ads_cpv").attr('src','');
                        $("#div_ads_cpv").hide();
                        clearTimeout(timejob);
                    }
                },1000)
            }
        }


    })
}


